const container = document.getElementById('root');
const ajax = new XMLHttpRequest();
const NEWS_URL = "https://api.hnpwa.com/v0/news/1.json";
const CONTENT_URL = "https://api.hnpwa.com/v0/item/@id.json"; // ë§ˆí‚¹ @id
const content = document.createElement('div');
// ì—¬ëŸ¬ í•¨ìˆ˜ì— ê±¸ì³ ê³µìœ ë˜ëŠ” ìì›ë“¤ì„ í•˜ë‚˜ë¡œ ë¬¶ì–´ ë†“ëŠ”ë‹¤. 
// ë‹¤ë¥¸ ë°ì´í„°ë“¤ë„ ì¶”ê°€ë ìˆ˜ ìˆê¸° ë•Œë¬¸ì—
const store = {
  currentPage : 1, // í˜„ì¬ í˜ì´ì§€ ì´ˆê¸°ê°’ 1
  maxPage : null,
};


// ì…ë ¥ì€ ì¸ìë¡œ ë°›ì„ ìˆ˜ ìˆìŒ
