function getData(url) {
  ajax.open( 'GET', url, false );
  ajax.send();
  return JSON.parse(ajax.response);
}


// ë¼ìš°í„°ë€ - ì¤‘ê³„ê¸° (í™”ë©´ì´ ì—¬ëŸ¬ê°œê°€ ìˆë‹¤ê³  í•˜ë©´ Aìƒíƒœ Bìƒíƒœë¥¼ ìƒí™©ì— ë§ê²Œ ë³´ì—¬ì£¼ëŠ” ê²ƒ)
// ê¸€ ëª©ë¡í™”ë©´ì˜ ì½”ë“œë¥¼ í•¨ìˆ˜ë¡œ ë¬¶ì–´ì£¼ì–´ì•¼ í•œë‹¤. 
// ëª©ë¡ í™”ë©´ì„ ë³´ì—¬ì£¼ëŠ” í•¨ìˆ˜
function newsFeed() {
  const newsFeed = getData(NEWS_URL); // ë°ì´í„°ë¥¼ ê°€ì ¸ì˜¤ëŠ” ì½”ë“œ
  const newsList = [];
  store.maxPage = Math.round(newsFeed.length / 10);
  console.log(store.maxPage);

  // ì½”ë“œì˜ ì–‘ì€ ëŠ˜ì–´ë‚˜ë”ë¼ë„ ë³µì¡ë„ê°€ ëŠ˜ì–´ë‚˜ì§€ ì•Šë„ë¡ ì‘ì—…í•´ì•¼ í•œë‹¤.
  // ë‚´ìš©ì´ ë³µì¡í•´ì§€ë©´ ë¬¸ì œë°œìƒ ...  ë°°ì—´ì„ ìµœì†Œí™” --> í…œí”Œë¦¿ ë°©ì‹ ì‚¬ìš© (ì£¼ì¡° , ëª°ë”© ìƒê°)
  // UIê°€ ì–´ë–¤ êµ¬ì¡°ì¸ì§€ ëª…í™•í•˜ê²Œ ì•Œ ìˆ˜ ìˆë‹¤. + Markingë„ ì˜ í™•ì¸ í•  ìˆ˜ ìˆìŒ
  // ì¦‰, ë³µì¡ë„ë¥¼ ì¤„ì¼ ìˆ˜ ìˆë‹¤.
  let template = `
    <div class="bg-gray-600 min-h-screen">
      <div class="bg-white text-xl">
        <div class="mx-auto px-4">
          <div class="flex justify-between items center py-6">
            <div class="flex justify-start">
              <h1 class="font-extrabold">Hacker News</h1>
            </div>
            <div class="items-center justify-end">
              <a href="#/page/{{__prev_page__}}" class="text-gray-500">Previous</a>
              <a href="#/page/{{__next_page__}}" class="text-gray-500">Next</a>
            </div>
          </div>
        </div>
      </div>
      <div class="p-4 text-exl text-gray-700">
        {{__news_feed__}}
      </div>
    </div>
  `;

  for (let i = (store.currentPage - 1) * 10 ; i < store.currentPage * 10; i++) {
    // DOM API ì œê±°ì‹¤ìŠµ
    newsList.push(`
      <div class="p-6 ${newsFeed[i].read ? 'bg-red-500' : 'bg-white'} mt-6 rounded-lg shadow-md transition-colors duration-500 hover:bg-green-100">
        <div class="flex">
          <div class="flex-auto">
            <a href="#/show/${newsFeed[i].id}">${newsFeed[i].title}</a>  
          </div>
          <div class="text-center text-sm">
            <div class="w-10 text-white bg-green-300 rounded-lg px-0 py-2">${newsFeed[i].comments_count}</div>
          </div>
        </div>
        <div class="flex mt-3">
          <div class="grid grid-cols-3 text-sm text-gray-500">
            <div><i class="fas fa-user mr-1"></i>${newsFeed[i].user}</div>
            <div><i class="fas fa-heart mr-1"></i>${newsFeed[i].points}</div>
            <div><i class="far fa-clock mr-1"></i>${newsFeed[i].time_ago}</div>
          </div>  
        </div>
      </div>    
    `)
  }

  template = template.replace('{{__news_feed__}}', newsList.join(''));
  template = template.replace('{{__prev_page__}}', store.currentPage > 1 ? store.currentPage -1 : 1);
  template = template.replace('{{__next_page__}}', store.currentPage + 1);
  
  container.innerHTML = template;
}

// ë‰´ìŠ¤ ìƒì„¸ ë¶€ë¥´ëŠ” í•¨ìˆ˜
function newsDetail() { // í•´ì‹œê°’ì´ ë³€ê²½ë  ê²½ìš°ì— í•¨ìˆ˜ í˜¸ì¶œ
  const id = location.hash.substring(7);
  const newsContent =  getData(CONTENT_URL.replace('@id', id));
  const title = document.createElement('h1');
  let template = `
    <div class="bg-gray-600 min-h-screen pb-8">
      <div class="bg-white text-xl">
        <div class="mx-auto px-4">
          <div class="flex justify-between items-center py-6">
            <div class="flex justify-start">
              <h1 class="font-extrabold">Hacker News</h1>
            </div>
            <div class="items-center justify-end">
              <a href="#/page/${store.currentPage}" class="text-gray-500">
                <i class="fa fa-times"></i>
              </a>
            </div>
          </div>
        </div>
      </div>

      <div class="h-full border rounded-xl bg-white m-6 p-4 ">
        <h2>${newsContent.title}</h2>
        <div class="text-gray-400 h-20">
          ${newsContent.content}
        </div>

        {{__comments__}}

      </div>
    </div>
  `;


  function makeComment(comments, called = 0) {
    const commentString = [];

    for (let i = 0; i < comments.length; i++) {
      commentString.push(`
        <div style="padding-left: ${called * 40}px;" class="mt-4">
          <div class="text-gray-400">
            <i class="fa fa-sort-up mr-2"></i>
            <strong>${comments[i].user}</strong> ${comments[i].time_ago}
          </div>
          <p class="text-gray-700">${comments[i].content}</p>
        </div>    
      `)
      // ì»¤ë©˜íŠ¸ ìì²´ì— ì»¤ë©˜íŠ¸ê°€ ë˜ ìˆì„ ìˆ˜ ìˆë‹¤.
      if(comments[i].comments.length > 0) {
        commentString.push(makeComment(comments[i].comments, called + 1)); // ì¬ê·€í˜¸ì¶œ --> ìµìˆ™í•´ì ¸ì•¼ í•˜ëŠ” êµ¬ì¡°
      }
    }

    return commentString.join('');
  }
  container.innerHTML = template.replace('{{__comments__}}', makeComment(newsContent.comments));
}

// ë¼ìš°í„° - í™”ë©´ì´ ì „í™˜ë˜ì–´ì•¼ í•  ë•Œë¥¼ íŒë‹¨
// ê¸°ì¡´ì— hashchangeë¥¼ í†µí•´ í™”ë©´ ì „í™˜ íŠ¸ë¦¬ê±°ë¥¼ ì‚¬ìš©í–ˆìŒ
// í•´ì‹œê°€ ë°”ë€Œë©´ ë¬´ì¡°ê±´ ê¸€ ë‚´ìš©ì„ ë³´ëŠ” ê²ƒì´ë‹¤ (ì§€ê¸ˆê¹Œì§€ëŠ”)
// í•´ì‹œê°€ ë°”ë€”ë•Œë§ˆë‹¤ routerí•¨ìˆ˜ê°€ í˜¸ì¶œë˜ë„ë¡ ë³€ê²½
function router() {
  // ì‹œì‘í•˜ìë§ˆì ê²Œì‹œê¸€ì„ í•œë²ˆ ë³´ì—¬ì£¼ì–´ì•¼ í•œë‹¤.
  
  // ì‹¤ì œë¡œ í™”ë©´ì„ ì „í™”í•˜ëŠ” ê²ƒì´ ëª©ì 
  const routePath = location.hash;
  // ì´ˆê¸° hashê°’ì´ ì—†ìœ¼ë©´ newsFeed í•¨ìˆ˜ë¥¼ í˜¸ì¶œí•˜ê³ 
  // '#' ë§Œ ë“¤ì–´ìˆì„ ê²½ìš°ì—ëŠ” ë¹ˆê°’ì„ ë°˜í™˜í•¨
  if(routePath === '') {
    // ëª©ë¡í™”ë©´ì—ì„œ ë„˜ì–´ê°ˆë•Œ í˜„ì¬ currentPageê°’ì„ ì‚¬ìš©
    newsFeed();
  } else if (routePath.indexOf('#/page/') >= 0) { // hashê°’ì´ ì¡´ì¬í•œë‹¤ë©´ newsDetailí•¨ìˆ˜ë¥¼ í˜¸ì¶œ
    store.currentPage = Number(routePath.substring(7));
    console.log(routePath.substring(7))
    // í˜ì´ì§€ë„¤ì´ì…˜
    // store.currentPage = 2; // í•˜ë“œì½”ë”©
    newsFeed();
  } else {
    newsDetail();
  }

}

// ê¸€ ë‚´ìš© í™”ë©´ - í•¨ìˆ˜ë¡œ êµ¬ì„±ë¨
// ì´ë²¤íŠ¸ í•¸ë“¤ëŸ¬í•¨ìˆ˜ê°€ ë¬¶ì—¬ ìˆì„ ê²½ìš° ë°–ìœ¼ë¡œ ë¹¼ë‚¼ ë°©ë²•ì´ ì—†ë‹¤..? (ë¬´ìŠ¨ ëœ». ìµëª…í•¨ìˆ˜ë¡œ ë˜ì–´ìˆìœ¼ë©´ ë¶€ë¥¼ ë°©ë²•ì´ ì—†ë‹¤.)
window.addEventListener('hashchange', router);


router(); // ì´ˆê¸°ì— router í•¨ìˆ˜ë¥¼ í˜¸ì¶œ